@import 'normalize.css';
@import url('https://fonts.googleapis.com/css2?family=Hubballi&family=Montserrat:wght@300;500;600&family=Roboto:ital,wght@1,300&display=swap');

:root {
    --primary: white;

    //default layout
    --default-layout-header-height: 60px;
    --default-layout-weight: 1150px;

}


html *::-webkit-scrollbar {
    border-radius: 0;
    width: 8px;
}

html *::-webkit-scrollbar {
    border-radius: 0;
    width: 8px;
}

html *::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(22, 24, 35, .6);
}

html *::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(22, 24, 35, 0.6);
}

html *::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
}

html *::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    font-size: 62.5%;
}

body {
    font-family: 'Hubballi', cursive;
    font-family: 'Montserrat', sans-serif;
    font-family: 'Roboto', sans-serif;
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
}


.fontawesome {
    margin: 1px;
    min-height: 20px;
    min-width: 20px;
}

a[href] {
    text-decoration: none;
}

a:visited,
a:link {
    color: black;

}

